@import url('https://fonts.googleapis.com/css?family=Titillium+Web');
@import url('https://fonts.googleapis.com/css?family=Montserrat');

.bd-placeholder-img {
  font-size: 1.125rem;
  text-anchor: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@media (min-width: 768px) {
  .bd-placeholder-img-lg {
    font-size: 3.5rem;
  }
}

.brand {
  display: inline-block;
  padding-top: .3125rem;
  padding-bottom: .3125rem;
  margin-right: 1rem;
  font-size: 1.25rem;
  line-height: inherit;
  white-space: nowrap;
  color: #fff;
  &:hover {
    text-decoration: none;
    color: #fff;
  }
}

.name-style {
  font-family: 'Titillium Web', sans-serif;
  position: relative;
  text-align: center;
  color: white;
  top: 20rem;
  h3 {
    font-style: italic;
    font-size: 1.4rem;
  }
  h1 {
    font-size: 4rem;
    font-weight: 700;
    letter-spacing: 2.5px;
  }
}

.home-image {
  background-color: rgba(200,100,0,.5); // Tint color
  background-blend-mode: multiply;
}

.title-text {
  padding: 50px 0 50px 0;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-size: 30px;
}

.title-text-sml {
  padding: 50px 0 20px 0;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-size: 30px;
}

.large-padding {
  padding-left: 50px;
  padding-right: 50px;
}

.work-image {
  width: 150px;
}

.pb {
  padding-bottom: 3rem;
}

.project {
  font-family: 'Montserrat', sans-serif;
}

.sm-padding-btm {
  padding-bottom: 10px;
}

.linkedin {
    margin: auto;
    display: block;
    width: 100px;
}

.lmb {
  margin-bottom: 50px;
}